class Stock{
    @observable data = ["dragonQuest","finalFantasy"];
    @observable counter = 0;
    @observable rest = [];

    increase() {
        this.counter = this.counter + 1;
    }
    decrease() {
        this.counter = this.counter - 1;
    }

    setdata(val) {
        console.log(val);
        let a = [];
        a.push(val);
        console.log(a)
    }



}


let stock = new Stock;

export default stock;

